footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  background: #336699;
  color: #f9f9f9;
  max-height: 5em;
  padding: 1em 1em;
}

footer a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
}

@media screen and (min-width: 700px) {
  footer {
    padding: 1em 3em;
  }
}



